

//@import "./image/services/top-right-bg.svg";

.services{ 
    text-align: right;  
    position: relative;
    padding: 120px 0 30px; 
    background-repeat: no-repeat;
    background-size: 100% 100%;
    //background-image: url("../about/image/services/top-right-bg.svg");

    .font_main{
        font-size: 30px;
    }

    .border{ 
        width: 100%;
    }
}
.services .content-info{ margin-top: 50px;}

.services .ovarlay{
    position: absolute;
    left: 0;
    top: 0;
    transform: rotate( 
180deg);
    height: 50%;
    width: 100%;
    background: linear-gradient( 
180deg, transparent 20%, #f6f7f8  100%);
}

.services .content-items .items{
    background-color: white;
    padding: 20px 10px;
    margin-bottom: 10px;
    cursor: pointer;
    border-radius: 5px;
    transition: .25s all ease-in-out;
}

.services .content-items .items:hover{
    box-shadow: 0 12px 20px 0 rgb(0 0 0 / 10%);
    background: -webkit-gradient(linear, left top, left bottom, from(#EFFAFF), to(#FFFFFF));
    background: -o-linear-gradient(top, #EFFAFF 0%, #FFFFFF 100%);
    background: linear-gradient(
180deg
, #EFFAFF 0%, #FFFFFF 100%);
    border-radius: 10px;
}

.services .content-items .items i{ font-size: 45px;}
.services .content-items .items h3{ 
    font-size: 28px; 
    color: #01072e;
    font-weight: 900; 
    margin-top: 40px; 
    margin-bottom: 7px;
}
.services .content-items .items p{ font-size: 18px;}
.services .marginn{ margin-top: -75px;}





/***************************(skills)***************/

.skills .heading{ 
    margin-bottom: 80px; 
    padding-top: 30px;

    .font_main{
        font-size: 30px;
    }
}
.skills .heading .border{ width: 22%; margin: 0 auto 15px;}
.skills .content-skills{
    padding-top: 30px;
    background: white;
    padding: 10px;
}
.skills .items{
    padding: 8px;
    border-radius: 5px;
    transition: .5s all ease-in-out;
    -webkit-transition: .5s all ease-in-out;
    -moz-transition: .5s all ease-in-out;
    -ms-transition: .5s all ease-in-out;
    -o-transition: .5s all ease-in-out;
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    -ms-border-radius: 5px;
    -o-border-radius: 5px;
}
.skills .items:hover{ background-color: rgba(8, 8, 8, 0.623);  color: white;  cursor: pointer;}
.skills .items img{ width: 100%; height: 150px;}
.skills .items h3{ font-size: 29px;
  color: #2f2e41;
  font-weight: bold;
  margin-top: 40px;
  margin-bottom: 20px;}
.skills .items p{ font-size: 17px;}
.skills .items:hover p{ color: white;}


