
footer {
    background: #ffffff;
    padding: 20px 0;
    margin-top: 50px;


    .bord{
        width: 90%;
        height: 1px;
        background-color: black;
        border-radius: 5px;
        -webkit-border-radius: 5px;
        -moz-border-radius: 5px;
        -ms-border-radius: 5px;
        -o-border-radius: 5px;
        margin: 30px auto;
}

    article{

        p{
        margin-top: 20px;
            a{
                margin-left: 10px;
                color: white;

                .fa{
                    padding: 5px;
                    border-radius: 50%;
                    width: 30px;
                    height: 30px;
                    line-height: 23px;
                }

                .fa-facebook{ background-color: blue;}
                .fa-twitter{ background-color: aqua;}
                .fa-google-plus{ background-color: lightcoral;}
                .fa-instagram{ background-color: chocolate;}

                &:hover{
                    color: white;
                }
            }

        }
    }

    .news{
        text-align: right;
        padding: 8px;
        border-radius: 10px;
        background: #dae3e4;

        .center{
            input[type="text"]{
                width: 100%;
                border: 0;
                padding: 12px 5px;
                border-radius: 5px;
                -webkit-border-radius: 5px;
                -moz-border-radius: 5px;
                -ms-border-radius: 5px;
                -o-border-radius: 5px;
                margin: 20px 0;
            }

            input[type="checkbox"]{
                margin-left: 8px;
            }
        }

        .bottom{
            margin: 20px 0;

            button{
                padding: 10px 40px;
            }
        }
    }

    .copy-right{
        p .a_bord{
            border-left: 1px solid black;
            border-right: 1px solid black;
            margin: 0 10px;
        }
    }
}