
aside{
    position: fixed;
    width: 280px;
    right: -208px;
    text-align: right;
    background-color: #1e2128;
    height: 100%;
    transition: .5s;
    -webkit-transition: .5s;
    -moz-transition: .5s;
    -ms-transition: .5s;
    -o-transition: .5s;
    top: 0;
    z-index: 5;

    &:hover{
        right: 0;
    }

    .Icon_Side{
        text-align: left;
        margin-bottom: 50px;
        border-color: transparent;
        background: rgba(0, 0, 0, 0.3);
        padding: 20px 0 20px 13px;
        font-size: 20px;

        a{ 
            color: white;
            margin-left: 15px !important;
        }
    }
}



aside .scroll-sidebar{ 
    //padding: 0 20px;

    ul{ 
        padding: 0;

        li{ 
            margin-bottom: 15px;
            padding: 10px 0; 

            &:hover{
                background: rgba(0, 0, 0, 0.3);
            }
            a{ 
                color: white; 
                font-size: 16px; 
                font-weight: bold;
                text-decoration: none;
                padding-right: 20px;

                i{ 
                    padding-left: 30px;     
                    float: left;
                }
            }
        }
    }
}