.content_head{
    width: 80%;
    margin: auto;
    margin-bottom: 30px;

    .train{
        padding: 5px 0;
        background-color: #54bad145;
        border-radius: 5px;

    
        ul{
            display: flex;
            justify-content: space-evenly;
            margin: 0;
            padding: 0;

            li{
                //margin-left: 30px;
                color: white;
                padding: 5px;
                border-radius: 5px;
                button{
                    margin-right: 20px;
                    border: 0;
                }
            }
            .child_1{
                background-color: #2e2e2ed4;
                button{
                    background-color: #222222;
                }
            }
            .child_2{
                background-color: rgb(0, 159, 170);
                button{
                    background-color: rgb(4, 100, 104);
                }
            }
            .child_3{
                background-color: rgb(42, 197, 68);
                button{
                    background-color: rgb(22, 134, 7);
                }
            }
            
        }
    }


    .heading{
        background: white;
        width: 70%;
        //margin-top: 20px;
        padding: 10px;
        margin: 25px auto 40px;
        border-radius: 50px;
        -webkit-border-radius: 50px;
        -moz-border-radius: 50px;
        -ms-border-radius: 50px;
        -o-border-radius: 50px;
    }
}