
.navbar{
    background-color: #051934 !important;
    position: fixed;
    width: 100%;
    z-index: 5;
    left: 0;
    top: 0;
}
/*Shrinking for mobile*/
@media (max-width: 768px) {
    .navbar .navbar-nav{ padding: 0;}
}
.navbar .navbar-nav .nav-item .dropdown-menu{ 
    text-align: right; min-height: 100px; 
    font-family: 'Lalezar';
    max-height: 570px; overflow: auto;}

.navbar .navbar-nav .nav-item .afterr::after{
    position: absolute;
    content: "";
    top: -27px;
    right: 14px;
    border-width: 15px;
    border-style: solid;
    border-color: transparent transparent #ffffff transparent;
}
.navbar .navbar-nav .nav-item .dropdown-menu a{ padding-right: 10px; }
.navbar .navbar-nav .nav-item .dropdown-menu button{ padding-right: 10px; }
.topbar { 
    height: 4.375rem; 
    margin: 0 !important; 
    background-color: #1e2128 !important;
    //z-index: 1;
    color: black;
    box-shadow: 0px 2px 11px #000000a6 !important;
}

.topbar .nav-item .nav-link { height: 4.375rem; display: flex; align-items: center; padding: 0 .75rem;}
.topbar .nav-item .nav-link .badge-counter { 
    position: absolute; 
    transform: scale(.7); 
    transform-origin: top right;
    right: .25rem;
    margin-top: -.25rem;
}
.topbar.navbar-light .navbar-nav .nav-item .nav-link { color: #d1d3e2;}
.topbar.navbar-light .navbar-nav .nav-item .nav-link i{font-size: 30px;}
.dropdown-toggle::after { content: none !important;}

.badge {
    display: inline-block;
    padding: .25em .4em;
    font-size: 75%;
    font-weight: 700;
    line-height: 1;
    text-align: center;
    white-space: nowrap;
    vertical-align: baseline;
    border-radius: .35rem;
    transition: color .15s ease-in-out, background-color .15s ease-in-out, border-color .15s ease-in-out, box-shadow .15s ease-in-out;
    -webkit-transition: color .15s ease-in-out, background-color .15s ease-in-out, border-color .15s ease-in-out, box-shadow .15s ease-in-out;
    -moz-transition: color .15s ease-in-out, background-color .15s ease-in-out, border-color .15s ease-in-out, box-shadow .15s ease-in-out;
    -ms-transition: color .15s ease-in-out, background-color .15s ease-in-out, border-color .15s ease-in-out, box-shadow .15s ease-in-out;
    -o-transition: color .15s ease-in-out, background-color .15s ease-in-out, border-color .15s ease-in-out, box-shadow .15s ease-in-out;
}

