.rounded-circle { width: 40px; height: 40px;}
.topbar .topbar-divider { width: 0; border-right: 1px solid #e3e6f0; height: calc(4.375rem - 2rem); margin: auto 1rem;}
.topbar .dropdown-list { width: 20rem !important;}


.navbar-right-top .nav-item { border-right: 1px solid #e6e6f2;}
.navbar-right-top .nav-item:last-child { border: none;}
.navbar-right-top .nav-item .nav-link { padding: 13px 20px; font-size: 16px; line-height: 2; color: #82849f;}
.top-search-bar { padding-top: 12px; padding-right: 24px;}
.notification-dropdown { min-width: 320px;}
.notification-dropdown, .connection-dropdown, .nav-user-dropdown { padding: 0px;}

.notification-title {
    font-size: 14px;
    color: #3d405c;
    text-align: center;
    padding: 8px 0px;
    border-bottom: 1px solid #e3e3e3;
    line-height: 1.5;
    background-color: #fffffe;
}

.notification-list .list-group-item { 
    border-radius: 0px; 
    padding: 12px; 
    margin-top: -1px; 
    border-left: transparent;
    border-right: transparent;
}

.notification-list .list-group-item.active { z-index: 2; color: #3d405c; background-color: #f7f7fb; border-color: #e1e1e7;}
.notification-list .list-group-item-action:focus,
.list-group-item-action:hover { color: #404040; text-decoration: none; background-color: #f7f7fb;}
.notification-info .notification-date {
    display: block;
    font-size: 11px;
    margin-top: 4px;
    text-transform: uppercase;
    color: #71748d;
}

.notification .dropdown-toggle::after,
.connection .dropdown-toggle::after,
.nav-user .dropdown-toggle::after {
    display: inline-block;
    width: 0;
    height: 0;
    margin-left: .255em;
    vertical-align: .255em;
    content: "";
    border: none;
}

.notification-list-user-img { float: right;}
.notification-list-user-block { padding-right: 50px; font-size: 14px; line-height: 21px;}
.notification-list-user-name { color: #5969ff; font-size: 14px; margin-right: 8px;}
.list-footer,
.conntection-footer {
    font-size: 14px;
    color: #fff;
    text-align: center;
    padding: 10px 0px;
    line-height: 1.5;
    font-weight: 700;
    background-color: #009bfd;
    border-bottom-left-radius: 3px;
    border-bottom-right-radius: 3px;
}

.list-footer a,.conntection-footer a { color: #fff;}
.list-footer a:hover, .conntection-footer a:hover { color: #fff;}

.indicator {
    content: '';
    position: absolute;
    top: 16px;
    right: 23px;
    display: inline-block;
    width: 7px;
    height: 7px;
    border-radius: 100%;
    background-color: #ef172c;
    animation: .9s infinite beatHeart;
    transform-origin: center;
}

@keyframes beatHeart {
    0% { transform: scale(0.9); -webkit-transform: scale(0.9); -moz-transform: scale(0.9); -ms-transform: scale(0.9); -o-transform: scale(0.9); }

    25% { transform: scale(1.1); -webkit-transform: scale(1.1); -moz-transform: scale(1.1); -ms-transform: scale(1.1); -o-transform: scale(1.1); }

    40% { transform: scale(0.9); -webkit-transform: scale(0.9); -moz-transform: scale(0.9); -ms-transform: scale(0.9); -o-transform: scale(0.9); }

    60% { transform: scale(1.1); -webkit-transform: scale(1.1); -moz-transform: scale(1.1); -ms-transform: scale(1.1); -o-transform: scale(1.1); }

    100% { transform: scale(0.9); -webkit-transform: scale(0.9); -moz-transform: scale(0.9); -ms-transform: scale(0.9); -o-transform: scale(0.9); }
}


