body{   background-color: #ffffff;}

hr{ background-color: #e6d29d;}


.border{
    height: 6px;
    width: 30%;
    background-color: #2b044d;
    margin-bottom: 15px;
}

.btn_a{
    border: 0;
    outline: 0;
    border-radius: 0;
    background: #1f193a;
    font-size: 23px;
    padding: 8px 35px;
    color: white;
}

.btn-top{
    position: fixed;
    bottom: 20px;
    right: 20px;
    display: none;
    z-index: 2;
}


/***********************************( input-comment ) ************************/
.input-comment{ 
    padding: 40px 0 20px;
    background-color: white;
    width: 80%;
    margin: auto;
    border-radius: 10px;

    .font_main{
        font-size: 30px;
    }

    .font_child{
        font-size: 15px;
    }
}
.input-comment .heading{ margin-bottom: 60px;}
.input-comment .heading .border{ margin: 0 auto 15px; width: 15%;}
.input-comment .content-input-comment img{width: 100%;}
.input-comment .content-input-comment .items{
    background: #e0e0e0;
    border: none;
    border-radius: 7px;
    padding: 18px;
    outline: none;
    width: 100%;
}

.input-comment .content-input-comment textarea{
    background: #e0e0e0;
    border: none;
    border-radius: 20px;
    height: 150px;
    width: 100%;
    outline: none;
    padding: 10px;
}
.input-comment .content-input-comment input[type="submit"]{     font-family: 'Lemonada'; border-radius: 10px; -webkit-border-radius: 10px; -moz-border-radius: 10px; -ms-border-radius: 10px; -o-border-radius: 10px; }

::placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
    color: rgba(26, 26, 26, 0.548);
    opacity: 1; /* Firefox */
    font-size: 20px;
    
}

