.side_content{
    width: 20%;
    text-align: right;

    .ads{
        background: white;
        border-radius: 7px;

        .start{
            padding: 6px 10px;
            background-color: var(--sub_color);
            border-radius: 7px 7px 0 0 ;
            -webkit-border-radius: 7px 7px 0 0 ;
            -moz-border-radius: 7px 7px 0 0 ;
            -ms-border-radius: 7px 7px 0 0 ;
            -o-border-radius: 7px 7px 0 0 ;

            h4{
                color: white;
            }
        }

        .data_img{

            .img img{
                max-width: 100%;
                border-radius: 15px;
                padding: 10px;
            }
        }

        .main_data .items{
            margin-top: 10px;
            display: flex;
            justify-content: space-around;

            .item span{
                border: 1px solid #0a7cca;
                padding: 1px 10px;
                margin-right: 5px;
                color: #0a7cca;
                font-weight: 600;
            }
        }

        .bottom{

            button{
                width: 95%;
                margin-bottom: 10px;
            }

            ul{ padding: 0; }
            ul li{
                display: inline-block;
                margin-left: 5px;
                background-color: #bbc1bf;
                width: 10px;
                height: 10px;
                border-radius: 50%;
                -webkit-border-radius: 50%;
                -moz-border-radius: 50%;
                -ms-border-radius: 50%;
                -o-border-radius: 50%;
            }
            ul li.active{ background-color: #0a7ccd;}
        }
    }
}