
.font_main{ 
    font-family: 'Lalezar', cursive;
    color: var(--main_color);
    font-size: 20px;
    font-weight: 700;
    line-height: 1.3;
    margin: 0;
    display: inline-block;

}

.font_child{
    font-family: 'Almarai', sans-serif;
    color: #464c57;
    margin-bottom: 20px;
    font-size: 12px;
    line-height: 30px;
    font-weight: bold;
    //border-bottom: 1px solid black;
    padding: 0 5px;
}



/******************************************/

.content{
    width: 70%;
    margin-right: 5%;
    text-align: right;
    border: 1px solid rgba(156, 156, 156, 0.781);
    padding: 10px 0;

    .inp_serach{
        display: flex;
        background-color: #54bad145;
        border-radius: 5px;
        padding: 10px 5px;
        justify-content: space-around;
        align-items: center;
        position: relative;
        
        &::before{
            content: "";
            position: absolute;
            left: 50%;
            bottom: -40px;
            border-style: solid;
            border-width: 20px;
            border-color: #54bad145 transparent transparent transparent;
        }

        p{
            margin: 0;
        }

        .m{
            color: white;
            background: #16181cba;
            padding: 5px;
            border-radius: 5px;
            
            labal{
                margin-left: 5px;
            }
        }
    }

    i{
        color: var(--main_color);
        cursor: pointer;
    }

    .content_cources{ 
        background: white; 
        padding: 10px; 
        margin-bottom: 40px;
        margin-top: 30px;
        

        .product {
            position: relative;
            height: 400px;
            box-shadow: 0 20px 40px rgba(0, 0, 0, .2);
            border-radius: 5px;
            background: #1c1c22;
            overflow: hidden;
            margin: 0 auto 20px;

            &:hover .specifies{
                bottom: 0;
            }
        
            .imgbox{
                height: 100%;

                img { 
                    display: block; 
                    width: 100%;
                    height: 100%;
                }
            }

            .specifies {
                cursor: alias;
                position: absolute;
                width: 100%;
                bottom: -230px;
                background: #fff;
                padding: 10px;
                box-sizing: border-box;
                transition: .5s;
                -webkit-transition: .5s;
                -moz-transition: .5s;
                -ms-transition: .5s;
                -o-transition: .5s;

                .head{
                    padding: 10px 0;
                }
            /*
                &:hover{
                    bottom: 0;
                }
            */
            
                .data_cource{
                    display: flex;
                    justify-content: space-around;
                    border-bottom: 1px solid black;
                    .postion{
                        width: 50%;
                        span{
                            display: block;
                            margin-bottom: 5px;

                            i{
                                margin-left: 5px;
                            }
                        }
                    }
                    .name_com{
                        text-align: center;
                        width: 50%;

                        h5{
                            color: var(--main_color);
                        }
                    }
                }
                
                .content-btn{
                    display: flex;
                    justify-content: space-between;
                    margin-top: 10px;

                    .shoil_media{
                        .icon i{
                            margin-left: 10px;
                        }
                    }

                }
            
                a{
                    border-radius: 5px;
                    background: var(--main_color);
                    color: white;
                    padding: 10px 20px;
                    font-family: 'Almarai';
                    text-decoration: none;
                    -webkit-border-radius: 5px;
                    -moz-border-radius: 5px;
                    -ms-border-radius: 5px;
                    -o-border-radius: 5px;
                }
            }
                        
        }
    }
}


/*******************************************/



